.sidebar {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;
  background-color: #0d7eaf;
  height: 64px;
  backdrop-filter: saturate(180%) blur(20px);
  border-bottom: 1px solid #ccc;
  box-shadow: 0 1px 1px #eee;
  color: white;
  text-decoration: none;
}

.sidebar .container-logo-title {
  flex: 2;
  display: flex;
  align-items: center;
  padding: 0 15px;
  height: 55px;
}

.sidebar .container-logo-title .logo-caixa {
  height: 50px;
  width: 50px;
  padding-right: 12px;
}

@media (max-width: 1023px) {
  .sidebar .container-logo-title .logo-caixa {
    height: 30px;
    width: 30px;
    padding-right: 7px;
  }
}

.sidebar .container-logo-title .logo-misclientes {
  height: 30px;
  width: 45px;
  padding-left: 0.5rem;
  padding-top: 8px;
  padding-bottom: 8px;
}

@media (max-width: 1023px) {
  .sidebar .container-logo-title .logo-misclientes {
    display: none;
  }
}

.sidebar .container-logo-title .bar {
  width: 1px;
  height: 17px;
  border-right: 1px solid white;
}

.sidebar .container-logo-title .title-name {
  font-family: Arial, sans-serif;
  font-size: 16px;
  color: white;
  margin-right: 13px;
  margin-left: 8px;
  font-weight: bold;
  font-style: italic;
}

@media (max-width: 1023px) {
  .sidebar .container-logo-title .title-name {
    margin-right: 7px;
    margin-left: 7px;
  }
}

.sidebar .container-logo-title .title-page {
  font-family: Arial, sans-serif;
  font-size: 16px;
  padding: 0 12px;
}

@media (max-width: 1023px) {
  .sidebar .container-logo-title .title-page {
    font-size: 15px;
  }
}

.sidebar .container-logo-title .icon-filters {
  cursor: pointer;
  position: absolute;
  right: 20px;
  --opf-shape-circle: 0;
}

.sidebar .menu {
  flex: 1;
  justify-content: space-between;
}

.sidebar .menu nav.menu-links {
  display: flex;
  flex: 1;
  height: 39px;
}

.sidebar .menu nav.menu-links .link {
  margin: 0;
}

.sidebar .menu nav.menu-links .link-icon {
  height: 1rem;
  width: 1rem;
  fill: #fff;
}

.sidebar .menu nav.menu-links .link-a {
  display: flex;
  align-items: center;
  margin: 0;
  white-space: nowrap;
}

.sidebar .menu nav.menu-links .link-a span {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.sidebar .menu nav.menu-links .link-a:hover {
  background-color: #06587c;
}

.sidebar .menu nav.menu-links ul {
  display: flex;
  gap: 1rem;
  padding-left: 40px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.25rem !important;
  font-weight: 200 !important;
}

.sidebar .menu nav.menu-links ul li {
  list-style: none;
  padding-right: 5px;
  display: flex;
  align-items: center;
}

.sidebar .menu nav.menu-links ul li .item {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  color: white;
  text-decoration: none;
  border-radius: 3px;
  cursor: pointer;
}

.sidebar .menu nav.menu-links ul li .item span {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.sidebar .menu nav.menu-links ul li .item .arrow {
  transform: rotate(90deg);
  width: 100%;
  height: 100%;
}

.sidebar .menu nav.menu-links ul li .item:hover {
  background-color: #06587c;
}

.sidebar .menu nav.menu-links ul li a {
  margin-right: 15px;
  padding: 5px 10px;
  color: white;
  text-decoration: none;
  border-radius: 3px;
  cursor: pointer;
}

.sidebar .menu nav.menu-links ul li .active {
  background-color: #06587c;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs {
  display: none;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs {
  top: 94px;
  left: 0;
  width: 100%;
  position: absolute;
  z-index: 3;
  background-color: white;
  box-shadow: 0 2px 5px gray;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs {
  height: 40px;
  align-items: center;
  font-size: 1em !important;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li {
  height: 100%;
  padding: 0;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li a {
  cursor: pointer;
  height: 70%;
  width: 100%;
  margin: 0;
  background-color: #fff;
  text-decoration: none;
  border-radius: 3px;
  font-weight: 600;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li a span {
  padding-left: 0;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li .label-wrapper {
  height: 50%;
  display: flex;
  align-items: center;
  padding: 0.5rem;
  border-radius: 0.25rem;
  color: #06587c;
  background-color: #fff;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li.selected .label-wrapper {
  color: #fff;
  background-color: #06587c;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li:hover .label-wrapper {
  color: #fff;
  background-color: #06587c;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li:hover a.disabled {
  cursor: default;
  margin-right: 15px;
  padding: 3px 10px;
  color: #969696;
  text-decoration: none;
  border-radius: 3px;
  font-weight: 600;
}

.sidebar .menu nav.menu-links ul li .dropdown-childs .list-childs .link-childs .child-li:hover a.disabled:hover {
  color: #969696;
  background-color: #fff;
}

.sidebar .menu nav.menu-links ul .dropdown-childs:hover {
  display: flex;
}

.sidebar .menu nav.menu-links ul li:hover .dropdown-childs {
  display: flex;
}

#currentselections {
  flex: 1;
  position: relative;
  padding-left: 1.5rem;
}

#currentselections .qv-panel-current-selections {
  background-color: #0674ae;
  border-bottom: none !important;
}

#currentselections .qv-panel-current-selections .wrap {
  background-color: #0674ae;
  border-bottom: none !important;
}

#currentselections .bright.qv-panel-current-selections .buttons-end {
  display: none;
}

#currentselections .bright.qv-panel-current-selections .buttons.border-bottom {
  border-bottom: none;
}

#currentselections .bright.qv-panel-current-selections .no-selection {
  display: none;
}

#currentselections .qv-panel-current-selections .buttons .toggle-button {
  display: none;
}

#currentselections .qv-panel-current-selections .buttons .qv-subtoolbar-button.qv-subtoolbar-button {
  color: #eee;
}

#currentselections .bright.qv-panel-current-selections .item {
  color: #eee;
}

#currentselections .bright.qv-panel-current-selections li {
  background: #0674ae;
}

#currentselections .bright.qv-panel-current-selections .values :not(.item-locked) {
  color: #eee;
}

#currentselections .qv-panel-current-selections .remove span {
  font-size: 14px;
  color: #eee;
}

.sidebar-filters {
  right: -300px !important;
  position: fixed;
  width: 250px;
  top: 97px;
  background-color: #1c82a8;
  z-index: 5;
  font-family: openSans, serif;
  font-size: 16px;
  padding: 1.5rem;
  border-radius: 3px;
  box-shadow: 3px 3px 3px #aaa;
  transition: all 0.2s;
}

.sidebar-filters .title-filters {
  font-size: 0.95rem;
  color: white;
  fill: #fff;
  display: flex;
  margin-bottom: 1rem;
}

.sidebar-filters .filters {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.sidebar-filters .filters .filter-button {
  width: 100%;
  height: 2.75rem;
}

.sidebar-filters .filters .filter-button .qv-collapsed-listbox .title-wrapper {
  background-color: white;
  color: #1c82a8;
}

.sidebar-filters .filters .filter-button .title {
  color: #0d7eaf;
  text-align: left;
  font-weight: 100 !important;
}

.sidebar-filters .filters .filter-button .qv-collapsed-listbox .qv-state-count-bar {
  display: none;
}

.sidebar-filters .filters .filter-button .qv-collapsed-listbox.interactive:hover {
  border: 1px solid white;
}

.visible {
  transform: translateX(-305px);
}

.fondo-negro {
  position: absolute;
  left: 0;
  top: 0;
  background: black;
  width: 100vw;
  height: 100vh;
  z-index: 4;
  opacity: 0.5;
  display: flex;
}

.fondo-negro .fondo-boton {
  width: 100vw;
  height: 100vh;
  --opf-size-flex: 100%;
  --opf-button-box-shape: 0;
  --opf-spacing-200: 0;
  --opf-color-fill-text-button-primary-default: transparent;
  --opf-color-fill-text-button-primary-pressed: transparent;
  --opf-color-background-button-primary-default: transparent;
  --opf-color-background-button-primary-hover: transparent;
  --opf-color-background-button-primary-pressed: transparent;
}

@font-face {
  font-family: 'fontello';
  src: url("font/fontello.eot?70992583");
  src: url("font/fontello.eot?70992583#iefix") format("embedded-opentype"), url("font/fontello.woff?70992583") format("woff"), url("font/fontello.ttf?70992583") format("truetype"), url("font/fontello.svg?70992583#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^='icon-']::before,
[class*=' icon-']::before {
  font-family: 'fontello', serif;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-target_retos::before {
  content: '\e800';
}

.icon-billete_ventas::before {
  content: '\e801';
}
